.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
#name {
  color: red;
  font-size: xx-large;
  text-align: center;
  font-weight: bold;
}
#accordian {
  width: 80%;
  text-align: center;
  margin: 0 auto;
}
#piano {
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 25%;
}
#card {
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 25%;
  text-align: center;
}
#initial {
  display: block;
  margin-left: auto;
  margin-right: auto;
}
